input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

h1 {
  font-size: 2.6rem; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper main {
    flex-grow: 1; }

.scrollable {
  padding-bottom: 2.5rem;
  max-height: calc(100vh - 8rem);
  overflow-y: auto; }

/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #a7a7a7 transparent; }

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 0.3rem; }

*::-webkit-scrollbar-track {
  background: transparent; }

*::-webkit-scrollbar-thumb {
  background-color: #a7a7a7;
  border-radius: 20px;
  border: 3px solid transparent; }

p + p {
  margin-top: 2.4rem; }

.container {
  width: 100%;
  max-width: 137.76rem;
  margin: 0 auto;
  padding: 0 3.2rem; }
  @media (max-width: 768px) {
    .container {
      padding: 0 2rem; } }
  .container--no-padding {
    padding: 0; }
    @media (max-width: 768px) {
      .container--no-padding {
        padding: 0; } }

.header {
  position: fixed;
  width: 100%;
  z-index: 10;
  background-color: var(--color-blue); }
  .header .burger {
    margin-right: 1.8rem; }

.header__container {
  display: flex;
  flex-wrap: wrap;
  padding: 1.6rem 0;
  gap: 1.5rem; }

.header__wrappper {
  display: flex;
  align-items: center;
  transition: opacity var(--default-transition); }
  .header__wrappper:hover {
    opacity: 0.8; }
  .header__wrappper:last-child {
    margin-left: auto; }

.header__img {
  width: 4.8rem;
  height: 4.8rem; }
  @media (max-width: 768px) {
    .header__img {
      width: 3.8rem;
      height: 3.8rem; } }

.header__title {
  margin-left: 2.4rem;
  color: var(--color-default-white); }
  @media (max-width: 768px) {
    .header__title {
      display: none; } }

.main {
  display: flex;
  flex-direction: column;
  padding-top: 8rem; }
  .main > .container {
    display: flex;
    flex-direction: column;
    flex-grow: 1; }
  @media (max-width: 768px) {
    .main {
      padding-top: 7rem; } }

.main__container {
  display: grid;
  grid-template-columns: 30rem 1fr;
  flex-grow: 1;
  position: relative; }
  @media (max-width: 768px) {
    .main__container {
      grid-template-columns: 1fr; } }

.menu {
  padding-top: 2.5rem;
  padding-left: 3.2rem;
  box-shadow: 0.6rem 0 0.5rem -0.6rem rgba(2, 0, 96, 0.25);
  background-color: var(--color-default-white); }
  @media (max-width: 768px) {
    .menu {
      display: none;
      position: absolute;
      padding-left: 2rem; } }
  @media (max-width: 768px) {
    .menu.active {
      display: block;
      height: calc(100vh - 8rem);
      width: 100%; } }

.menu__title {
  margin-bottom: 3.2rem;
  font-size: 2rem; }

.menu__list {
  display: flex;
  flex-direction: column;
  gap: 4rem; }

.menu__item {
  position: relative; }
  .menu__item--expand {
    position: relative;
    overflow: hidden;
    cursor: pointer; }
    .menu__item--expand::after {
      content: '';
      position: absolute;
      width: 1.3rem;
      height: 1.3rem;
      right: 1.5rem;
      top: 1rem;
      background-image: url(".././img/svg/ic_arrow-expand.svg");
      transform: rotate(180deg);
      transition: transform var(--default-transition); }
    .menu__item--expand.expanded::after {
      transform: rotate(0); }
    .menu__item--expand .menu__link {
      display: flex; }
    .menu__item--expand .menu__link-title {
      user-select: none; }
  .menu__item.active::after {
    content: '';
    position: absolute;
    width: 0.3rem;
    height: 3.7rem;
    right: 0;
    top: -0.5rem;
    background-color: var(--color-blue);
    border-radius: 0.5rem 0 0 0.5rem; }

.menu__inner-list {
  margin-top: 3.2rem;
  gap: 3.2rem;
  padding-left: 4.8rem;
  padding-right: 0.5rem; }

.menu__link {
  display: inline-flex;
  align-items: center;
  transition: opacity var(--default-transition);
  font-weight: 600; }
  .menu__link:hover {
    opacity: 0.8; }

.menu__icon {
  width: 3.2rem;
  height: 3.2rem; }
  @media (max-width: 768px) {
    .menu__icon {
      width: 2.4rem;
      height: 2.4rem; } }

.menu__link-title {
  padding: 0.5rem 0.5rem 0.5rem 1.6rem;
  font-weight: 700; }

.burger {
  display: none;
  position: relative;
  width: 36px;
  border: 1px solid var(--color-blue); }
  @media (max-width: 768px) {
    .burger {
      display: block; } }

.burger {
  border-radius: 0.5rem; }

.burger__open,
.burger__close {
  position: absolute;
  left: 6px; }

.burger__close:after {
  opacity: 0; }

.burger__open:after,
.burger__close:after,
.burger__close:before {
  content: "";
  position: absolute;
  background: var(--color-blue);
  width: 22px;
  height: 2px;
  transition: opacity var(--default-transition); }

.burger__open:after {
  box-shadow: 0 -7px var(--color-blue), 0 7px var(--color-blue); }

.active .burger__open:after {
  opacity: 0; }

.active .burger__close:after {
  opacity: 1; }

.active .burger__close:after {
  transform: rotate(-45deg); }

.active .burger__close:before {
  transform: rotate(45deg); }

.content {
  padding: 2.5rem 3.2rem 3.2rem 3.2rem; }
  @media (max-width: 768px) {
    .content {
      padding: 2rem; } }
  .content .button {
    min-width: 21.7rem; }
    @media (max-width: 768px) {
      .content .button {
        min-width: 18rem; } }

.content__title {
  margin-bottom: 1.6rem; }

.content__sub-title {
  margin-bottom: 2.4rem; }

.content__info a {
  color: #002BC5;
  text-decoration: underline; }

.content__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 2.4rem;
  margin-bottom: 3.2rem; }

.content__input {
  flex-grow: 1; }

.input {
