  font-weight: 700;
  border-radius: 1rem;
  cursor: pointer;
  transition: opacity var(--default-transition); }
  .button:hover {
    opacity: 0.8; }
  @media (max-width: 768px) {
    .button {
      padding: 1.3rem;
      font-size: 2rem; } }

.button__img {
  margin-right: 1.6rem;
  width: 3.2rem;
  height: 3.2rem; }
  @media (max-width: 768px) {
    .button__img {
      width: 2.4rem;
      height: 2.4rem; } }

.search-result {
  padding: 2.4rem;
  border-radius: 1rem;
  box-shadow: 0 0.2rem 0.8rem rgba(2, 0, 97, 0.25); }

.search-result__title {
